import React from 'react';
import tinycolor from 'tinycolor2';
import { css } from 'react-emotion';
import marked from 'marked';

import { GRAY_000 } from '../lib/style-constants';

marked.setOptions({
  gfm: true,
});

const ISSUE_CARD_STYLE = css({
  display: 'block',
  cursor: 'pointer',
  ':hover': {
    backgroundColor: GRAY_000,
  },
});

const NOTE_CONTENT_STYLE = css({
  '>p:first-child': {
    marginTop: 0,
  },
});

const AVATAR_COLLECTION_STYLE = css({
  display: 'flex',
  alignItems: 'center',
});

const AVATAR_STYLE = css({
  borderRadius: '50%',
  objectFit: 'cover',
  width: '1.5rem',
  height: '1.5rem',
  marginRight: '0.25rem',
  display: 'block',
});

const ATTRIBUTION_ROW_STYLE = css({
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  minHeight: '1.5rem',
});

const LABEL_ROW_STYLE = css({
  display: 'flex',
  flexWrap: 'wrap',
});

const LABEL_STYLE = css({
  borderRadius: 2,
  textTransform: 'uppercase',
  fontFamily: 'Montserrat, Arial, sans-serif',
  fontWeight: 'bold',
  fontSize: '10px',
  padding: '0.25rem',
  marginRight: '0.5rem',
});

function renderNoteCard(card) {
  const creator = card.creator;

  return (
    <div className="p-a200">
      <div
        className={`t--info ${NOTE_CONTENT_STYLE}`}
        dangerouslySetInnerHTML={{ __html: marked(card.note) }}
      />
      <div className="m-t200">
        <div className="t--subinfo" style={{ lineHeight: 1 }}>
          Created by <a href={creator.htmlUrl}>{creator.login}</a>
        </div>
      </div>
    </div>
  );
}

function renderIssueCard(issue) {
  const urlBits = issue.htmlUrl.split('/');
  // eslint-disable-next-line no-unused-vars
  const [repo, ignored, num] = urlBits.slice(-3);
  const opener = issue.user;
  const labels = issue.labels;
  const assignees = issue.assignees;

  // We can't nest <a> tags, so we make the outer <div> act like an <a> that
  // goes to the issue page.
  return (
    <div
      className={`${ISSUE_CARD_STYLE} p-a200`}
      onClick={ev => {
        ev.stopPropagation();
        if (ev.ctrlKey || ev.metaKey) {
          window.open(issue.htmlUrl, '_blank');
        } else {
          window.location = issue.htmlUrl;
        }
      }}
    >
      <h4 className="txt-l" style={{ marginTop: 0 }}>
        {issue.title}
      </h4>

      {labels.length > 0 && (
        <div className={`m-v100 ${LABEL_ROW_STYLE}`}>
          {labels.map(({ name, color }) => (
            <div
              key={name}
              className={`m-v100 ${LABEL_STYLE}`}
              style={{
                backgroundColor: `#${color}`,
                color: tinycolor(color).isDark() ? '#fff' : '#000',
              }}
            >
              {name}
            </div>
          ))}
        </div>
      )}

      <div className={`m-t100 ${ATTRIBUTION_ROW_STYLE}`}>
        <div className="t--subinfo" style={{ lineHeight: 1 }}>
          <a href={issue.htmlUrl}>
            {repo}#{num}
          </a>{' '}
          opened by <a href={opener.htmlUrl}>{opener.login}</a>
        </div>
        <div className={`${AVATAR_COLLECTION_STYLE}`}>
          {assignees.map(({ login, avatarUrl }) => (
            <img
              key={login}
              alt={login}
              title={`Assigned to ${login}`}
              src={avatarUrl}
              className={`${AVATAR_STYLE}`}
            />
          ))}
        </div>
      </div>
    </div>
  );
}

export default function Card({ card }) {
  return (
    <div className={`br br-a100 m-v200`}>
      {card.note && renderNoteCard(card)}
      {card.issue && renderIssueCard(card.issue)}
    </div>
  );
}

export const cardFragment = graphql`
  fragment CardFragment on GithubProjectCard {
    note
    creator {
      login
      htmlUrl
    }
    issue {
      number
      title
      htmlUrl
      state
      user {
        login
        htmlUrl
      }
      labels {
        name
        color
      }
      assignees {
        login
        htmlUrl
        avatarUrl
      }
    }
  }
`;



