import React from 'react';
import { css } from 'react-emotion';
import Helmet from 'react-helmet';
import { format } from 'date-fns';

import { MEDIA_SMALL } from '../lib/style-constants';
import Column from '../components/column';

const PAGE_STYLE = css({
  height: '100vh',
  display: 'flex',
  flexDirection: 'column',
  paddingBottom: 0,
});

const COLUMN_CONTAINER_STYLE = css({
  display: 'flex',
  flexDirection: 'column',
  flexGrow: 1,
  paddingRight: '1rem',

  [MEDIA_SMALL]: {
    flexDirection: 'row',
    overflow: 'scroll',
    paddingRight: 0,
  },
});

export default function ProjectTemplate({ data }) {
  const project = data.githubProject;

  const updatedString = format(new Date(), '[Generated on] M/D/YYYY');

  return (
    <div className={`${PAGE_STYLE}`}>
      <Helmet title={project.name} />
      <div className="p-a300">
        <div className="sh" style={{ flexShrink: 0 }}>
          <h1 className="sh-title">{project.name}</h1>
          <span className="sh-contact">{updatedString}</span>
        </div>
      </div>

      <div className={`${COLUMN_CONTAINER_STYLE.toString()} m-t500`}>
        {project.columns.map(column => (
          <Column key={column.id} column={column} />
        ))}
      </div>
    </div>
  );
}

export const query = graphql`
  query ProjectQuery($slug: String!) {
    githubProject(fields: { slug: { eq: $slug } }) {
      name
      columns {
        id
        ...ColumnFragment
      }
    }
  }
`;



