import React from 'react';
import { css } from 'react-emotion';

import { MEDIA_SMALL } from '../lib/style-constants';

import Card from './card';

const COLUMN_STYLE = css({
  minWidth: '20rem',
  marginLeft: '1rem',
});

const HEADER_STYLE = css({
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
});

const HEADER_COUNT_STYLE = css({
  backgroundColor: 'white',
  borderRadius: '1em',
  minWidth: '1.25em',
  textAlign: 'center',
  padding: '0.25rem 0.5em 0.15rem',
});

const CARDS_CONTAINER_STYLE = css({
  display: 'none',
  cursor: 'pointer',
  [MEDIA_SMALL]: {
    display: 'block',
    cursor: 'default',
  },
  '.brg-tr:checked ~ &': {
    display: 'block',
  },
});

export default function Column({ column }) {
  const checkId = `column-show-${column.id}`;
  return (
    <div className={`${COLUMN_STYLE}`}>
      <input
        type="checkbox"
        id={checkId}
        className="brg-tr"
        aria-hidden="true"
      />

      <label className={`b--b p-a300 m-b200 ${HEADER_STYLE}`} htmlFor={checkId}>
        <h3 className="h3 tt-u" style={{ color: 'white' }}>
          {column.name}
        </h3>
        <div className={`h3 ${HEADER_COUNT_STYLE}`}>{column.cards.length}</div>
      </label>

      <div className={`${CARDS_CONTAINER_STYLE}`}>
        {column.cards.map(card => <Card key={card.id} card={card} />)}
      </div>
    </div>
  );
}

export const columnFragment = graphql`
  fragment ColumnFragment on GithubProjectColumn {
    name
    cards {
      id
      ...CardFragment
    }
  }
`;



